import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';

export default function SuccesCard({task}){
    return(
        <Card sx={{ minWidth: 275, borderColor:"rgb(127,255,0,0.5)" }}>
      <CardContent>
        <Typography sx={{ fontSize: 14 }} color="text.secondary" gutterBottom>
          Последняя обработка
        </Typography>
        <Typography variant="h5" component="div">
          Обработка от {task.time}
        </Typography>
        <Typography sx={{ mb: 1.5 }} color="text.secondary">
          Успешно: {task.isValidCount} Ошибка: {task.inValidCount} Категория не указана: { task.notDetected }
        </Typography>
        <Typography variant="h5" color="rgb(127,255,0)">
          Завершено
          <br />
        </Typography>
      </CardContent>
      <CardActions>
        <Button size="small">Посмотреть резульат</Button>
      </CardActions>
    </Card>
  );
}